import {Context, Point} from "../Drawing/Graphics";
import {Resources} from "../Resources";
import {StandardCursor, CursorBase} from "../Cursors/cursors.module";
import {Button} from "../Objects/objects.module";

export class StaticScene{
    private _startGameMenuItem : Button;

    public items : Array<any>;
    public cursor : CursorBase;

    constructor(){
        this.items = [];

        this.cursor = new StandardCursor(0,0,5,5);

        this._startGameMenuItem = new Button(450,350,200,50);
        this._startGameMenuItem.middlePoint = new Point(560,315);
        this._startGameMenuItem.text = "Spiel starten";
        this.items.push(this._startGameMenuItem);
    }

    public draw(context : Context) : void{
        context.drawImage(Resources.desert, 0,0,context.width, context.height);
        
        this.items
            .filter(i => i.draw != undefined)
            .forEach(go => go.draw(context));
    }

    public update() : void{
        this.items
            .filter(i => i.update != undefined)
            .forEach(go => go.update());
    }
}